@use "../util" as *;

h1,
h2,
h3 {
	margin-top: 0;
	line-height: 1.1;
}

a,
a:visited,
a:active {
	text-decoration: none;
}

.section__title {
	font-size: 2rem;
	font-weight: 600;
	text-align: center;
	margin: 0 1em;
	// color: var(--blue);
	background: linear-gradient(to left, hsl(201, 100%, 50%), hsl(207, 100%, 30%));
	background-size: 200% auto;
	color: red;
	background-clip: text;
	// text-fill-color: transparent;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	animation: shine 4s linear infinite;
	@keyframes shine {
		to {
			background-position: 200% center;
		}
	}
	@include breakpoint(medium) {
		font-size: 3em;
	}
}

.section__description {
	margin: auto;
	margin-block: 1em;
	text-align: center;
}
